import React from 'react';
import { FormGroup, Input, Label, FormFeedback } from 'reactstrap';
import { ErrorMessage } from 'formik';
import PropTypes from 'prop-types';

InputField.propTypes = {
    field: PropTypes.object.isRequired,
    form: PropTypes.object.isRequired,

    type: PropTypes.string,
    label: PropTypes.string,
    placeholder: PropTypes.string,
    disabled: PropTypes.bool,
};

InputField.defaultProps = {
    type: 'text',
    label: '',
    placeholder: '',
    disabled: false,
}


function InputField(props) {
    // L·∫•y d·ªØ li·ªáu c·ªßa Control Bind v√†o UI Control ( Input)
    const { field, form, // cua formik
    type, label, placeholder, disabled  // cua minh custom
    } = props;
    // const{name, value, onChange, onBlur} = field; // 4 thu quan trong cua 1 control
    const{name} = field; 

    const {errors, touched} = form; //error va touched cua form
    const showError = errors[name] && touched[name]; //khi c√≥ l·ªói v√† ta ƒë√£ touch v√†o control n√†y th√¨ ta show error
    return (
        <FormGroup>
            {label && <Label for={name}>{label}</Label>}

            <Input 
            id={name}
            // name={name}         // === ...field
            // value= {value}
            // onChange={onChange}
            // onBlur={onBlur}
            {...field}
            type={type}
            disabled={disabled}
            placeholder={placeholder}
            invalid={showError}
            />

        {/* {showError && <FormFeedback>{errors[name]}</FormFeedback>}  ch·ªâ show khi th·∫±ng tr∆∞·ªõc n√≥ c√≥ invalid */}
        {/* c·ªßa formik */}
        <ErrorMessage name={name} component={FormFeedback} /> 
        
        </FormGroup>
    );
}

export default InputField;